.image4{
    background: rgba(158, 157, 157, 0.3);
    height: 50vh;
    width: 100%;
    position: relative;
}

.image4::before{
    content: '';
    background: url('https://www.mumbaipropertyexchange.com/images/builders/projects/28949_main.jpg?height=350') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.image4 .heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.image4 h1{
    font-size: 40px;
    color: wheat;
}

/* .image3 p{
    font-size: 40px;
    color: wheat;
} */

#outer
{
    width:100%;
    text-align: center;
}
.inner
{
    display: inline-block;
}

.inner .com{
    list-style-type: none;
    background: wheat;
    padding-top: 5px;
    padding-left: 8px;
    padding-bottom: 5px;
    padding-right: 8px;
    margin-left: 10px;
    border-radius: 5px;
    margin-top: 5px;
  }
  .com a{
    color: #000;
    font-weight: 100;
  
  }
  .com:hover{
    background:rgb(249, 195, 94);
  }

  .inner .loc{
    list-style-type: none;
    background: #5ba545;
    padding-top: 5px;
    padding-left: 8px;
    padding-bottom: 5px;
    padding-right: 8px;
    margin-left: 720px;
    border-radius: 5px;
    margin-top: 5px;
  }

  .loc a{
    color: #fff;
    font-weight: 100;
  
  }
  .loc:hover{
    background:rgb(249, 195, 94);
  }

  .features{
    padding: 50px 12%;
    font-size: 22px;
}

.rows{
    display: flex;
    width:100%;
    align-items: center;
    flex-wrap: wrap;
    padding: 50px 0;
}

.text-col{
    flex-basis: 50%;
    margin-bottom: 20px;
    text-align: justify;
}

.img-col{
    flex-basis: 50%;
    margin-bottom: 20px;
}

.img-col img{
    display: block;
    width: 90%;
    margin: auto;
}

#gallery{
    padding-top: 50px;
    margin-bottom: 50px;
}

#amenities{
    padding-top: 70px;
    margin-bottom: 50px;
}

/* .nities{
    margin-top: 10px;
} */
#location{
    padding-top: 50px;
}
#location,h2{
    text-align: center;
    font-size: 24px;
}

#location h1{
    font-weight: bold;
}

#location #add{
    font-weight: normal;
}

#location #loc_add{
    font-size: 20px;
}

#floorplan{
    padding-top: 50px;
    margin-bottom: 50px;
}
#floorplan img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
}
#fitnesscenter{
    padding-top: 50px;
    margin-bottom: 50px;
}
#fitnesscenter img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
}

#entrancelobby{
    padding-top: 50px;
    margin-bottom: 50px;
}

#entrancelobby img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
}

#amenities img{
    width: 100px;
    height: 100px;
    vertical-align: middle;
}

#maha_rera #maha_p{
    font-size: 20px;
    text-align: center;
}

#maha_rera #maha_p #maha_a:hover{
    color: blue;
}

#maha_a:visited{
    color: violet;
}

#maha_a:link{
    color: gray;
}

#rowA{
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;  
    padding-left: 150px;
}

#map-bar3 {
    margin-left: auto;
    margin-right: auto;
    width: 68%;
}

#map-bar3 iframe {
    width: 80%;
    text-align: center;
}

@media only screen and (max-width:980px) {

    .text-col, .img-col{
        flex-basis: 100%;
    }
    
    .features p{
        font-size: 15px;
    }

    .rows:nth-child(2), .rows:nth-child(4){
        flex-direction: column-reverse;
    }

    .features .rows{
        padding: 10px 0;
    }

    .heading h1,p{
        text-align: center;
    }

    #gallery{
        padding-top: 20px;
        margin-bottom: 70px;
    }

    #fitnesscenter{
        padding-top: 16px;
        margin-bottom: -17px;
    }

    #fitnesscenter img{
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 300px;
        height: 300px;
    }

    #entrancelobby{
        padding-top: 32px;
        margin-bottom: 70px;
    }
    #entrancelobby img{
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 300px;
        height: 300px;
    }

    #map-bar3 {
        margin-left: auto;
        margin-right: auto;
        width: 100%;
    }

    #map-bar3 iframe {
        width: 80%;
        text-align: center;
    }

    
}
@media only screen and (max-width:800px){
    .rows,.text-col,h2{
        text-align: center;
    }

    #floorplan img{
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 300px;
        height: 300px;
    }

    #map-bar3 {
        margin-left: auto;
        margin-right: auto;
    }

    #map-bar3 iframe {
        width: 80%;
        text-align: center;
    }
}

@media only screen and (max-width:1024px){
    .loch1{
        padding-top: 30px;
    }
}
